<template>
  <div class="box">
    <el-form @submit.native.prevent ref="form" :model="form" :rules="rules" class="login-container" :inline="true" label-width="70px">
    <h3 class="login_title">系统登录</h3>
    <el-form-item label="用户名" prop="username" >
      <el-input ref="userInput" v-model="form.username" placeholder="请输入用户名"></el-input>
    </el-form-item>
    <el-form-item label="密码" prop="password" >
      <el-input v-model="form.password" placeholder="请输入密码" type="password"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" style="margin-left:105px;" @click="submit" native-type="submit">登录</el-button>
    </el-form-item>
  </el-form>
  </div>
</template>

<script>
import Cookie from 'js-cookie'
import {getMenu} from '../api'
export default {
    data () {
        return {
            form:{
                username:'',
                password:''
            },
            rules:{
                username:[
                    {
                        required:true,trigger: 'blur',message: '请输入用户名'
                    }
                ],
                password:[
                    {
                        required:true,trigger: 'blur',message: '请输入密码'
                    }
                ]
            }
        }
    },
    methods: {
        submit(){
             this.$refs.form.validate((valid)=>{
                if(valid){
                    getMenu(this.form).then(({ data })=>{
                       if(data.code === 20000){
                        //token信息存入cookie用于不同页面间的通信
                         Cookie.set('token',data.data.token)
                         //跳转到首页
                        this.$router.push('/home')
                        // 清空表单信息
                        this.$refs.form.resetFields();
                        //获取菜单的数据，存入store中
                        this.$store.commit('setMenu',data.data.menu)
                        console.log(data.data);
                        console.log(data.data.menu);
                        this.$store.commit('addMenu',this.$router)
                        }else{
                            //报错提醒
                            this.$message.error(data.data.message);
                        }
                    })
                }
             })
        },
        onFocus(){
            this.$nextTick(() => this.$refs.userInput.focus())
        }
    },
    mounted () {
        this.onFocus()
    }
}
</script>

<style lang="less" scoped>
.box{
    width: 100%;
    height: 100vh;
    background: url(../assets/images/1e.jpeg) center/cover;
    overflow: hidden;
    .login-container{
    width: 350px;
    border: 1px solid #eaeaea;
    margin: 180px auto;
    padding: 35px 35px 15px 35px;
    background-color: #fff;
    border-radius: 15px;
    box-sizing: border-box;
    box-shadow: 0 0 25px #eac6c6;
    .el-input{
        width: 198px;
    }
    .login_title{
        margin-bottom: 40px;
        text-align: center;
        color: #505458;
    }
}
}
</style>